<template>
  <header class="preview-header">
    <!-- <el-button class="back-btn" size="mini"
        @click="back"
            ><i class="el-icon-arrow-left"></i>返回</el-button
        > -->
    <n-breadcrumb class="breadcrumb" separator="/">
      <n-breadcrumb-item v-for="item in list" :key="item.id">{{
        item.name
      }}</n-breadcrumb-item>
    </n-breadcrumb>
    <i class="el-icon-refresh refresh" @click="refresh"></i>
  </header>
</template>

<script>
import { debounce } from '@/utils'
export default {
  components: {},
  props: {
    pageId: {
      type: String,
      require: true,
    },
    menu: {
      type: Array,
      default: [],
    },
  },
  data() {
    return {}
  },
  created() {},
  mounted() {},
  methods: {
    back() {
      rootApp.$router.go(-1)
    },
    menuMap(menu, linkObj, parentData) {
      if (menu instanceof Array) {
        menu.forEach((item) => {
          this.menuMap(item, linkObj)
        })
      } else {
        const { id, type, pages } = menu
        // 分组
        if (type === 2) {
          pages?.forEach((item) => {
            this.menuMap(item, linkObj, menu)
          })
        }
        // 页面
        else {
          const linkArr = linkObj[id] || []
          parentData && linkArr.push(parentData)
          linkArr.push(menu)
          linkObj[id] = linkArr
        }
      }
      return linkObj
    },
    refresh: debounce(function () {
      this.$emit('refresh')
    }, 100),
  },

  computed: {
    list() {
      return this.menuMap(this.menu, {})[this.pageId]
    },
  },

  watch: {},
}
</script>
